<script>
import recordHead from '@/components/recordHead.vue'
import datetime from '@/components/datetime.vue'
import { date } from 'quasar'
import {mapGetters} from 'vuex';
export default {
  data(){
    return{
      model:new Date(),//
      timer:false//
    }
  },
  components: {
    "record-head":recordHead,
    datetime
  },
  methods:{
     // 
    newtime(newtime){
      if(newtime){
        this.model = newtime
      }
      this.timer = false
    }
  },
  computed:{
    // 
    thisdata(){
      return  `${date.formatDate(this.model, 'MM')}${date.formatDate(this.model, 'DD')}`
    },
    ...mapGetters([
      "thislang"
    ])
  }
}
</script>

